/* eslint-disable */

import React, {useEffect, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';

import './Detail.css'

let 박스 = styled.div`
    padding-top : 20px;
`;
let 제목 = styled.h4`
    font-size : 25px;
    color : ${ props => props.color }
`;

function Detail(props) {
  
  let [alert_state, alert_state_edit] = useState(true);
  let [input_state, input_state_edit] = useState('');
  
  useEffect(()=>{
      let timer = setTimeout(()=>{
        alert_state_edit(false);
      }, 2000)
      return ()=>{ clearTimeout(timer) }
      // return function(){ 'when unmount callback' }
  }, []);

  

  let { id } = useParams();
  let page_history = useHistory();


  let get_shoe = props.shoes.find(function(shoe){
      return shoe.id == id
  });
  let get_stock = props.stock[id]
  

  function stockEdit() {
    debugger;
    let copy_arr = [...props.stock]
    copy_arr[id] -= 1
    if(copy_arr[id] < 0) {
      alert("재고 소진");
      return;
    }
    
    props.stock_edit(copy_arr)
  }



  return(
    <div className="container">
      <박스>
        <제목 className='red'>Detail</제목>
      </박스>

      <input onChange={(e)=>{input_state_edit(e.target.value)}} />

      {
        alert_state ?
        <div className='my-alert'>
          <p>재고가 얼마 남지 않았습니다</p>
        </div> 
        : null
      }
      
      <div className="row">
        <div className="col-md-6">
          <img src={get_shoe.img_src} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{get_shoe.title}</h4>
          <p>{get_shoe.content}</p>
          <p>{get_shoe.price}원</p>
          <INFO_STOCK></INFO_STOCK>
          <button className="btn btn-danger" onClick={ stockEdit }>주문하기</button> 
          <button className="btn btn-danger" onClick={ ()=>{ page_history.goBack() } }>뒤로가기</button> 
        </div>
      </div>
    </div>
  )

  function INFO_STOCK() {
    return(
      <p>재고 : {get_stock} </p>
    )
  }
  
}

export default Detail;